// home.js
import '../less/home.less';
import $ from 'jquery';
import 'fullpage.js';
import Swiper from 'swiper';
$(document).ready(function() {
    $('body').fadeIn();
    $('#fullpage').fullpage({
        navigation:true,
        navigationPosition: 'right',
        sectionsColor: ['#fff', '#fff', '#fff', '#F9F9F9', '#fff'],
        anchors: ['page1', 'page2', 'page3', 'page4', 'page5'],
        afterLoad: function(anchorLink, index) {
            let $header = $('.bee-header');
            if(index > 1){
                if(!$header.hasClass('black')){
                    $header.addClass('black')
                }
            }else{
                if($header.hasClass('black')){
                    $header.removeClass('black')
                }
            }
        },
        afterRender: function () {
            // $('.part1').find('.down').bind('click', function () {
            //     $.fn.fullpage.moveSectionDown()
            // });
            $('.part1').find('.video').html('<video id="mediaElementID" src="http://image.yscredit.com/www/index_video.mp4" width= 100%; height=100%; style="object-fit: cover" autoplay loop>您的浏览器不支持 video 标签。</video>');
            document.getElementById('mediaElementID').currentTime = 1; //设定在 1seconds
        }
    });
    var swiper = new Swiper('.swiper-container', {
        slidesPerView: 4,
        spaceBetween: 20,
        slidesPerGroup: 4,
        loop: true,
        loopFillGroupWithBlank: true,
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
    });
    $('.background1_img1,.background1_img2,.background2_img1,.background2_img2,.background3_img1,.background3_img2').hover(function () {
        $(this).find('.shadow').hide();
    },function () {
        $(this).find('.shadow').show();
    });
    $('#play').click(function () {
        let $modal_rule_win = $('.modal_rule_win');
        $modal_rule_win.find('.rule_content').html('<video src="http://image.yscredit.com/www/index_video.mp4" controls autoplay>您的浏览器不支持 video 标签。</video>');
        $modal_rule_win.css({'display':'flex'});
    });
    $('.close_btn').click(function(){
        let $modal_rule_win = $('.modal_rule_win');
        $modal_rule_win.find('.rule_content').html('');
        $modal_rule_win.css({'display':'none' });
    })
});


